<template>
  <div class="PhotosView">
      <img
        class="PhotosView__image"
        :key="index"
        :src="photo"
        v-for="(photo, index) in photos"
      >
      <br>
      <div>
        <input type="file" accept="image/*" @change="savePhoto">
      </div>
  </div>
</template>

<script>
export default {
    name: 'PhotoView',

    created() {
        this.$store.dispatch('loadPhotos')
    },

    computed: {
        photos() {
            return this.$store.getters.project.photos;
        }
    },

    methods: {
        savePhoto(event) {
            this.$store.dispatch('savePhoto', event.target.files[0])
        }
    }
}
</script>

<style lang="scss" scoped>
.PhotosView {
    overflow-y: scroll;

    &__image {
        border: 2px solid grey;
        height: auto;
        padding: 2px;
        width: 40%;
    }
}
</style>